"use strict";

exports.arc = function(ctx) {
    return function(a) {
        return function() {
            ctx.arc(a.x, a.y, a.radius, a.start, a.end, a.anticlockwise);
        };
    };
};

exports.cosh = function(a) {
    return Math.cosh(a);
}

exports.sinh = function(a) {
    return Math.sinh(a);
}

exports.acosh = function(a) {
    return Math.acosh(a);
}
